






























import {Component, Vue, Watch} from 'vue-property-decorator';
import {Button, Col, Field, Row, Divider} from 'vant';
import ScanSign from '@/componets/scan_sign.vue';
import {notes, NoteType} from '@/util/note';

import 'vant/lib/field/style';
import 'vant/lib/row/style';
import 'vant/lib/col/style';
import 'vant/lib/button/style';
import 'vant/lib/divider/style';
import {CashboxJs} from '@/util/cashboxjs'
import {CertGia, CertOwn} from "../../model/certs_model";
import {DiamondUtil} from "../../util/diamond_util";
import {checkAddressInfo} from "../../util/utils";
import {ToastUtil} from "../../util/toast";

Vue.use(Field).use(Row).use(Col).use(Button).use(Divider);

@Component({
    components: {ScanSign},
})

export default class Input extends Vue {
    public inputMessage = '';
    public giaNum = "";
    public showApplyBtn = true;
    public signState = false; // 暂替签名状态
    public nextBtnShow = true;
    public bodyHeight = 0;
    private certOwn!: CertOwn;

    public created(): void {
        this.$store.state.title = this.$t('ownTitle.input');
        {
            (window as any).nativeSignMsgToJsResult = this.nativeSignMsgToJsResult;
        }
    }

    public mounted() {
        this.bodyHeight = document.documentElement.clientHeight * 93 / 100;  // 除去7%的title高度
        this.certOwn = this.$store.state.nowCertOwn;
        this.giaNum = this.certOwn.certGia.gia;
    }

    @Watch('signState', {immediate: true, deep: true})
    private stateChange() {
        if (this.signState) {
            this.nextBtnShow = true;
        } else {
            this.nextBtnShow = false;
        }
    }

    public async onClickSelfSign() {
        if (this.inputMessage) {                              // 检查上链信息是否输入
            this.$store.commit('updateCertAttachInputData', this.inputMessage);
            //let nativeAddress = "5DHob83qDK5KWgz9xeqXcyYpku7bnLWR2cuRzKQpkY4p7BzL";  // todo 测试时候用： 业务员 freda 身份
            this.$store.commit('updateNativeChainAddress', this.$store.state.nativeChainAddress);
            if (checkAddressInfo(this.$store.state.nativeChainAddress)) {
                let res = await DiamondUtil.getInstance().assembleAddCertOwnInfo(this.giaNum, this.inputMessage, this.$store.state.nativeChainAddress);
                console.log("assembleAddCertOwnInfo===>" + res);
                CashboxJs.getInstance().callNativeSignMsgToJs(res);
            } else {
                ToastUtil.fail("当前钱包的地址，不能用于此条签名");
            }
        } else {
            notes.add({type: NoteType.Error, message: this.$t('notifyMsg.inputNineteen').toString()});
        }
        this.showApplyBtn = true;
    }

    public nativeSignMsgToJsResult(res: string): void {
        console.log("own_input nativeSignMsgToJsResult===>" + res);
        this.$store.commit('updateNowCertSignedInfo', res);
        this.$router.push({name: 'own_confirm'});
    }
}
